        }"
        :action-list="[
          { text: 'Âõ∫ÂÆöË°®Â§¥', action: () => setFixedRow() },
          { text: 'Âõ∫ÂÆöÊúÄÂè≥ÂàôÂàó', action: () => setFixedRight() },
          { text: 'Â±Ö‰∏≠Ë°®Â§¥label', action: () => setLabelCenter() },
          { text: 'Ê∑ªÂä†ËæπÊ°Ü', action: () => setBorder() },
          { text: 'È´ò‰∫ÆÈÄâ‰∏≠Á¨¨0Ë°å', action: () => setCurrentRow(0) },
          { text: 'È´ò‰∫ÆÈÄâ‰∏≠Á¨¨1Ë°å', action: () => setCurrentRow(1) },
          { text: 'ÂèñÊ∂àÈÄâ‰∏≠Ë°å', action: () => setCurrentRow() }
        ]"
