      align: 'center',
      tableHeight: null,
      border: true,
      sourceList: {}
    }
  },
  computed: {
    mockData () {
      return {
        data: [
          { id: 0, name: 'ÁéãÂ∞èËôé1', isForbid: false },
          { id: 1, name: 'ÁéãÂ∞èËôé2', isForbid: false },
          { id: 2, name: 'ÁéãÂ∞èËôé3', isForbid: false },
          { id: 3, name: 'ÁéãÂ∞èËôé4', isForbid: false }
        ],
        pagination: {
          total: 3,
          pageSize: 10,
          currentPage: 1
        }
      }
    },
    config () {
      const self = this
      const align = self.align
      return [
        {
          attrs: {
            label: 'ÁºñÂè∑',
            align,
            prop: 'id'
          }
        },
        {
          attrs: {
            label: 'ÂêçÁß∞',
            prop: 'name',
            align,
            width: 200
          }
        },
        {
          attrs: {
            label: 'Áä∂ÊÄÅ',
            prop: 'isForbid',
            align,
            width: 200
          },
          // Ê∏≤ÊüìÂ≠óÁ¨¶‰∏≤ÔºåÈªòËÆ§‰∏çÊÉ≥Â±ïÁ§∫ prop ÁöÑÂÄºÔºåËÄåÊòØÊÉ≥ÂØπÂÆÉÂÅö‰∏Ä‰∫õÂ§ÑÁêÜÁöÑÊó∂ÂÄôÔºåÂèØ‰ª•Áî®Ëøô‰∏™ÊñπÊ≥ï
          render (isForbid) {
            return isForbid ? '‚úñÔ∏èÁ¶ÅÁî®‰∏≠' : '‚úîÔ∏èÈùûÁ¶ÅÁî®'
          }
        },
        {
          attrs: {
            label: 'ËØ¶ÊÉÖA',
            align,
            width: 400
          },
          // Ê∏≤ÊüìÁªÑ‰ª∂ÔºåËøîÂõûÂÄº‰∏∫‰∏Ä‰∏™Êï∞ÁªÑÔºå data ‰Ωú‰∏∫ÁªÑ‰ª∂ÁöÑ v-modelÔºåÈÄÇÁî®‰∫éÈúÄË¶ÅÂ±ïÁ§∫Â§çÊùÇÁöÑÊï∞ÊçÆÁöÑÂú∫ÊôØ
          renderComponent (row) {
            return [
              { name: 'TableChildrenA', data: row },
              { name: 'el-input', data: row.name },
              { name: 'el-rate', data: row.id }
            ]
          }
        },
        {
          attrs: {
            label: 'ËØ¶ÊÉÖB',
            align,
            width: 400
          },
          renderComponent (row) {
            return [
              { name: 'TableChildrenB', data: row }
            ]
          }
        },
        {
          attrs: {
            label: 'Êìç‰Ωú',
            width: '260',
            align,
            // ËÆæÁΩÆÂΩìÂâçÂàóÊÅ¢Â§çÁÇπÂáª‰∫ã‰ª∂ÂÜíÊ≥°
            // isBubble: false,
            fixed: self.fixedRight
          },
          // Ê∏≤Êüì el-buttonÔºå‰∏ÄËà¨Áî®Âú®ÊúÄÂêé‰∏ÄÂàó„ÄÇÁõÆÂâçÂè™ÊîØÊåÅ el-button Âíå click ‰∫ã‰ª∂ÔºåÂêéÁª≠‰ºöÊ†πÊçÆÈúÄÊ±ÇÊîØÊåÅ‰ªªÊÑèÁöÑ el-xxx Âíå‰∫ã‰ª∂ÂßîÊâò
          renderHTML (row) {
            return [
              {
                attrs: {
                  label: 'Êü•Áúã',
                  type: 'text',
                  size: 'medium'
                },
                el: 'button',
                click () {
                  this.$message(JSON.stringify(row))
                }
              },
              {
                attrs: {
                  label: 'ÁºñËæë',
                  type: 'text',
                  size: 'medium'
                },
                el: 'button',
                click () {
                  this.$message(`ÁºñÂè∑${row.id} router -> Â∑≤Ë∑≥ËΩ¨Âà∞ÁºñËæëÈ°µÈù¢ÔºÅ`)
                }
              },
              {
                attrs: {
                  label: 'ÂèëÂ∏É',
                  type: 'text',
                  size: 'medium'
                },
                el: 'button',
                click () {
                  this.setPublish(row)
                }
              },
              !row.isForbid ? {
                attrs: {
                  label: 'Á¶ÅÁî®',
                  type: 'text',
                  disabled: false,
                  size: 'medium'
                },
                el: 'button',
                click () {
                  this.setForbid(row)
                }
              } : {
                attrs: {
                  label: 'Ëß£Èô§Á¶ÅÁî®',
                  type: 'text',
                  disabled: false,
                  size: 'medium',
                  style: {
                    color: '#e6a23c'
                  }
                },
                el: 'button',
                click () {
                  this.setForbid(row)
                }
              },
              {
                attrs: {
                  label: 'Êåá‰ª§ÊµãËØï-Â§çÂà∂ÈìæÊé•',
                  type: 'primary',
                  size: 'medium',
                  // ‰∏∫ÁÆÄ‰æøËµ∑ËßÅÔºåËøôÈáåÂºïÂÖ•‰∫Ü clipboard Â∫ìÔºåËØ∑Ê≥®ÊÑè
                  directives: [
                    {
                      name: 'clipboard',
                      value: JSON.stringify(row),
                      arg: 'copy'
                    }
                  ]
                },
                el: 'button',
                click () {
                  this.copyLink(row)
                }
              }
            ]
          }
        }
      ]
    }
  },
  async created () {
    await this.getList()
  },
  methods: {
    sleep (time = 1000) {
      return new Promise((resolve) => setTimeout(resolve, time))
    },
    async getList () {
      this.loading = true

      await this.sleep()

      this.sourceList = this.mockData
      this.loading = false
    },
    setFixedRight () {
      if (!this.fixedRight) {
        this.fixedRight = 'right'
      } else {
        this.fixedRight = false
      }
    },
    setFixedRow () {
      if (!this.tableHeight) {
        this.tableHeight = '350'
      } else {
        this.tableHeight = ''
      }
    },
    setLabelCenter () {
      if (!this.align) {
        this.align = 'center'
      } else {
        this.align = ''
      }
    },
    setBorder () {
      this.border = !this.border
    },
    handleRowClick (row, column, cell) {
      this.$message({
        dangerouslyUseHTMLString: true, // Be careful :)
        message: `row-click ‰∫ã‰ª∂ÔºåÂçïÂáª‰∫Ü<span style="color: red;"> Á¨¨${row.$index}Ë°å </span>ËØ∑ÁúãÊéßÂà∂Âè∞ log`
      })
      console.log('ÂõûË∞ÉÂèÇÊï∞ÂàÜÂà´‰∏∫: row, column, cell')
      console.log(row, column, cell)
    },
    setCurrentRow (rowNumber) {
      const singleTable = this.$refs.singleTable
      const hocElTable = singleTable.$refs.hocElTable
      const row = rowNumber !== undefined ? this.sourceList.data[rowNumber] : ''
      hocElTable.setCurrentRow(row)
    },
    setPublish (row) {
      this.$confirm(`Ê≠§Êìç‰Ωú‰ºöÂ∞Ü${row.name}ÂèëÂ∏ÉÂà∞Á∫ø‰∏ä, ÊòØÂê¶ÁªßÁª≠?`, `ÁºñÂè∑${row.id}ÊèêÁ§∫`, {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: 'ÂèëÂ∏ÉÊàêÂäü!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Â∑≤ÂèñÊ∂àÂèëÂ∏É'
        })
      })
    },
    async setForbid (row) {
      this.loading = true
      await this.sleep()
      this.loading = false
      row.isForbid = !row.isForbid
    },
    copyLink (row) {
      this.$message({
        type: 'success',
        message: 'Êåá‰ª§ÊµãËØï-Â§çÂà∂ÊàêÂäüÔºåÂèØ‰ª•Á≤òË¥¥Âï¶ÔºÅ'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.box-container {
